#page3{
	height: auto;
}
#page3 .inner{
	padding: 0;
	margin: 0;
	margin: 0 auto;
	max-width: 1270px;
	position: relative;
	height: 100%;
	padding: 0 100px;
}
#page3 .title{
	text-align: center;
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s, transform 0.6s;
}
#page3.active .title,
#page3.active .list{
	opacity: 1;
	transform: translateY(0);
}

#page3 .list{
	padding: 70px 0;
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s, transform 0.6s;
	transition-delay:0.6s;
}

#page3 .main_tit{
	position: relative;
	display: inline-block;
	cursor: pointer;
}
#page3 .main_tit:hover,
#page3 .main_tit.active{
	color: #d73d3d;
}

#page3 .list .main_tit .txt{
	display: inline-block;
	vertical-align: top;
}
#page3 .list .main_tit h3{
	font-family: 'Montserrat', sans-serif;
	font-size: 3em;
	margin-bottom: 10px;
	font-weight: 600;
}
#page3 .list .main_tit .workBtn{
	position: absolute;
	top: 0;
	left: -60px;
	font-size: 3em;
	margin-right: 30px;
	display: inline-block;
	vertical-align: top;
}
#page3 .list .main_tit .txt span{
	padding: 0 5px;
    letter-spacing: 7px;
    font-size: 0.875em;
    color: #999898;
}
#page3 .container{
	display: none;

}
#page3 .container.active{
	display: block;

}




#page3 .contents{
	padding-bottom: 60px;
	/* border-bottom: 1px solid #aaa; */
	display: flex;
	margin-top: 50px;
	justify-content: space-between;
}

#page3 .contents .photo{
	width: 100%;
	display: flex;
	justify-items: center;
	height: 590px;
	position: relative;
}
#page3 .contents .photo.project1{
	background:url(./project1.png) no-repeat center center;
	background-size: contain;
	transition: 0.3s;
}
#page3 .contents .photo.mobile.project1{
	background:url(./project1_mobile.png) no-repeat center center;
	background-size: contain;
}
#page3 .contents .photo.project2{
	background:url(./project2.png) no-repeat center center;
	background-size: contain;
	transition: 0.3s;
}
#page3 .contents .photo.mobile.project2{
	background:url(./project2_mobile.png) no-repeat center center;
	background-size: contain;
}
#page3 .contents .photo.project3{
	background:url(./project3.png) no-repeat center center;
	background-size: contain;
	transition: 0.3s;
}
#page3 .contents .photo.mobile.project3{
	background:url(./project3_mobile.png) no-repeat center center;
	background-size: contain;
}
#page3 .contents .photo.project4{
	background:url(./project4.png) no-repeat center center;
	background-size: contain;
	transition: 0.3s;
}
#page3 .contents .photo.mobile.project4{
	background:url(./project4_mobile.png) no-repeat center center;
	background-size: contain;
}
#page3 .contents .photo .switchBtn{
	position: absolute;
	bottom: 60px;
	right: 0;
	transform: translateY(0);
	width: 40px;
	height: 40px;
	background: url(./switch_mobile.png) no-repeat 0 0;
	background-size: contain;
	text-indent: -9999px;
	overflow: hidden;
	animation: btnMoving 2s infinite;
}
#page3 .contents .photo .switchBtn:hover,
#page3 .contents .photo .switchBtn.active{
	background: url(./switch_mobile_hover.png) no-repeat 0 0;
	background-size: contain;
}
#page3 .contents .photo.mobile .switchBtn{
	background: url(./switch_pc.png) no-repeat 0 0;
	background-size: contain;
}
#page3 .contents .photo.mobile .switchBtn:hover,
#page3 .contents .photo.mobile .switchBtn:active{
	background: url(./switch_pc_hover.png) no-repeat 0 0;
	background-size: contain;
}
@keyframes btnMoving{
	0%{	transform: translateY(0);}
	50%{transform: translateY(5px);}
	100%{transform: translateY(0);}
}

#page3 .contents .project_list{
	width: 100%;
	padding-right: 50px;
	box-sizing: border-box;
}
#page3 .contents .project_list ul{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 25px 0;
	margin-top: 25px;
}

#page3 .contents .project_list li{
	position: relative;
	padding-top: 25px;
	width: 90%;
	border-bottom: 1px solid #aaa;
}

#page3 .contents .project_list li::after{
	content: "";
	position: absolute;
	width: 150%;
}

#page3 .contents .project_list li span{
	display: block;
	line-height: 2;
}
#page3 .contents .project_list li span.sub{
    font-size: 1.063em;
    font-weight: 600;
    color: #000;
}
#page3 .contents .project_list li span.desc{
	margin-bottom: 8px;
	font-size: 0.913em;
	color: #777;
	font-weight: 400;
}

#page3 .contents .project_list a{
	display: block;
    position: relative;
    margin-top: 50px;
    padding: 0 30px 0 0;
    width: 170px;
    line-height: 50px;
    text-align: center;
    font-size: 1em;
    background: #191919;
    color: #fff;
    transition: background-color 0.2s;
}
#page3 .contents .project_list a::after{
	content: "";
    position: absolute;
    left: 155px;
    top: 20px;
    width: 25px;
    height: 10px;
    background: url(./icon_next.png) no-repeat;
}


@media only screen and (max-width:1100px){
	#page3 .contents{
		display: block;
	}
	#page3 .contents .project_list ul{
		flex-direction: row;
	}
	#page3 .contents .project_list li{
		margin-left: 20px;
	}
	#page3 .contents .project_list li:first-child{
		margin-left: 0;
	}

	#page3 .contents .project_list a{
		margin: 0;
	}
}
@media only screen and (max-width:890px){
	#page3 .inner{
		padding:  0 30px;
	}
	#page3 .list{
		padding-top: 30px;
	}
	#page3 .list .main_tit h3{
		font-size: 1.75em;
	}
	#page3 .list .main_tit .workBtn{
		position: static;
		font-size: 1.75em;
		margin-right: 15px;
	}
	#page3 .contents .project_list {
		padding-right: 0;
	}
	#page3 .contents .project_list ul{
		flex-wrap: wrap;
		margin-top: 0;
	}
	#page3 .contents .project_list li span.sub{
		font-size: 1em;
	}
	#page3 .contents .project_list li span.desc{
		font-size: 0.875em;
	}
	#page3 .contents .project_list li:nth-child(2n+1){
		margin-left: 0;
	}
	#page3 .contents .project_list li{
		width: calc((100% - 20px)/2);
	}
	#page3 .contents{
		margin: 0;
		padding-bottom: 0;
	}
	#page3 .contents .photo{
		margin-top: 15px;
	}
}
@media only screen and (max-width:576px){
	#page3 .list .main_tit h3{
		font-size: 1.5em;
	}
	#page3 .list .main_tit .workBtn{
		font-size: 1.5em;
	}
	#page3 .list .main_tit .txt span{
		font-size: .7em;
		letter-spacing: 3px;
	}
	#page3 .contents .project_list li span.sub{
		font-size: .913em;
	}
	#page3 .contents .project_list li span.desc{
		font-size: 0.813em;
	}
}
@media only screen and (max-width:410px){
	#page3 .list .main_tit h3{
		font-size: 1.25em;
	}
	#page3 .list .main_tit .workBtn{
		font-size: 1.25em;
	}
	#page3 .contents .photo{
		height: 350px;
	}
	#page3 .contents .photo .switchBtn{
		bottom: 0;
	}
	#page3 .contents .project_list a{
		font-size: .813em;
		width: 130px;
	}
	#page3 .contents .project_list a::after{
		left: 120px;
	}
}