
#page4{
	height: auto;
	padding-top: 0;
	background: url(./contact.jpg) no-repeat 0 0;
	background-size:cover;
	background-attachment: fixed;
}
#page4 .inner{
	padding: 100px 0;
}
#page4 .background{
	position: relative;
}
#page4 .title{
	text-align: center;
	opacity: 0;
	transform: translateY(100px);
	transition: opacity 0.3s, transform 0.6s;
}
#page4.active .title,
#page4.active .contact .label{
	opacity: 1;
	transform: translateY(0);
}
#page4 .title .name{
	color: #222;
}
#page4 .contact{
	text-align: center;
}
#page4 .contact .label{
	padding: 100px 30px;
	opacity: 0;
	transform: translateY(100px);
	transition: opacity 0.3s, transform 0.6s;
	transition-delay: 0.6s;
} 
#page4 .contact .label .main{
	font-family: 'Montserrat', sans-serif;
	font-size: 2.5em;
	font-weight: 700;
	padding: 5px 0;
	line-height: 1.5;
	color: #2a2a2a;

}
#page4 .contact .label p{
	color: #2a2a2a;
}

#page4 .contact .with > div{
	display: inline-block;
	width: 40%;
	margin-left: 10%;
}

#page4 .contact .with .email{
	text-align: left;
	opacity: 0;
	transform: translateX(100px);
	transition: opacity 0.3s, transform 0.6s;
	transition-delay: 0.6s;
}
#page4 .contact .with p,
#page4 .contact .with p a{
	color: #d73d3d;
	font-size: 20px;
}
#page4 .contact .with .email p.main{
	font-size: 1.5em;
	margin-bottom: 30px;
	font-weight: 600;
}

#page4 .contact .with .phone{
	position: relative;
	text-align: right;
	margin-left: 0;
	opacity: 0;
	transform: translateX(-100px);
	transition: opacity 0.3s, transform 0.6s;
	transition-delay: 0.6s;
}
#page4 .contact .with .phone::after{
	content: "";
	position: absolute;
	right: -15%;
	top:-15%;
	width: 1px;
	height: 130%;
	background-color: #eaeaea;
}
#page4.active .contact .with .phone,
#page4.active .contact .with .email{
	opacity: 1;
	transform: translateX(0);
}
#page4 .contact .with .phone p.main{
	font-size: 1.5em;
	margin-bottom: 30px;
	font-weight: 600;
}

@media only screen and (max-width:890px){
	#page4 .contact .label .main{
		font-size: 1.75em;
	}
}
@media only screen and (max-width:576px){
	#page4{
		height: auto;
		padding-top: 0;
		background: url(./contact.jpg) no-repeat 0 0;
		background-size:cover;
		background-attachment: scroll;
	}
	#page4 .contact .label p{
		font-size: 0.875em;
	}
	#page4 .contact .label .main{
		font-size: 1.5em;
	}
	#page4 .contact .with > div{
		display: block;
		width: 100%;
	}
	#page4 .contact .with .phone{
		text-align: center;
		margin: 0;
		margin-bottom: 50px;
	}
	#page4 .contact .with .email{
		text-align: center;
		margin: 0;
	}
	#page4 .contact .with .phone::after{
		height: 1px;
		width: 60%;
		top: 90px;
		left: 50%;
		transform: translateX(-50%);
	}

}