#page1{
	width: 100%;
	height: 100vh;
	position: relative;
}
#page1 .inner{
	padding: 0 100px;
	height: 100%;
	max-width: 1270px;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: row-reverse;
}
#page1 .key_txt{
	font-size: 1em;
	padding-left: 30px;
	opacity: 0;
	transform: translateX(100px);
	transition: opacity 0.3s, transform 0.6s;
}
#page1 .key_txt .title{
	margin-bottom: 60px;
}


#page1 .key_txt .title .desc{
	position: relative;
	font-size: 1em;
	color: #666;
	font-weight: 300;
}
#page1 .key_txt .title .desc::after{
	content: "";
	position: absolute;
	left: 0;
	bottom: -30px;
	width: 100%;
	height: 1px;
	background-color: #787878;
}
#page1 .key_txt .info dt{
	font-size: 20px;
	color: #aaa;
	font-weight: 500;
	margin-bottom: 2.5em;
}
#page1 .key_txt .info dd{
	line-height: 3;
}
#page1 .key_txt .info dd img{
	vertical-align: middle;
	margin-right: 15px;
}

#page1 .photo{
	padding-top: 80px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	opacity: 0;
	transform: translateX(-100px);
	transition: opacity 0.3s, transform 0.6s;
}
#page1.active .key_txt,
#page1.active .photo{
	opacity: 1;
	transform: translateX(0);
}
#page1 .photo div{
	position: relative;
	max-width: 450px;
	margin-bottom: 50px;
	transition: all 0.4s;
	z-index: 1000;
}


#page1 .photo img{
	z-index: 1000;
	display: block;
	border-radius: 20px;
	box-shadow: 0 20px 20px -10px #ccc;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

#page1 .photo p{
	position: relative;
	text-align: center;
	line-height: 1.5;
}
#page1 .photo p::after{
	content: "";
	position: absolute;
	right: -20px;
	bottom: -10px;
	background: url(./quote1.png) no-repeat 0 0;
	background-size: contain;
	width: 30px;
	height: 30px;
}
#page1 .photo p::before{
	content: "";
	position: absolute;
	left: -10px;
	top: -20px;
	background: url(./quote2.png) no-repeat 0 0;
	background-size: contain;
	width: 30px;
	height: 30px;
}
#page1 .info p{
	text-align: left;
	line-height: 1.5;
}






@media only screen and (max-width:980px){
	section .title .name{
		font-size: 2.5em;
	}
	#page1 .photo p{
		font-size: 0.813em;
	}
}

@media only screen and (max-width:890px){
	#page1{
		height: auto;
	}
	#page1 .inner{
		position: relative;
		padding: 0 30px;
	}
	#page1 .key_txt{
		font-size: 0.75em;
		width: 100%;
	}
	#page1 .photo{
		width: 100%;
	}
	#page1 .key_txt{
		font-size: 0.813em;
	}
	#page1 .key_txt .info dt{
		font-size: 1.25em;

	}
}

@media only screen and (max-width:576px){
	#page1 .inner{
		flex-direction: column;
	}

	#page1 .photo{
		padding-top: 30px;
	}
	#page1 .photo p{
		font-size: .75em;
	}
	#page1 .photo p br.br{
		display: none;
	}
	#page1 .key_txt{
		font-size: .75em;
	}
	#page1 .photo div{
		width: 300px;
		height: 300px;
	}
}
@media only screen and (max-width:410px){
	section .title .name{
		font-size: 2em;
	}
}