#page2{
	background: url(./pageBg1.jpg) no-repeat 0 0;
	background-attachment: fixed;
	background-size: cover;
	overflow-x: hidden;
	height: auto;
	width: 100%;
}
#page2 .title{
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s, transform 0.6s;
}
#page2 .title .name .text div{
	display: inline;
}
#page2 .title .name .br{
	display: none;
}
#page2 .slider .left{
	width: 30%;
}

#page2 .slider{
	padding-top: 80px;
	display: flex;
	justify-content: space-between;
	opacity: 0;
	transform: translateY(50px);
	transition: opacity 0.3s, transform 0.6s;
	transition-delay: 0.6s;
}
#page2.active .title,
#page2.active .slider{
	opacity: 1;
	transform: translateY(0);
}
#page2 .slider .swiper {
	width: 100%;
	height: 350px;
	padding-left: 30px;
}

#page2 .slider .swiper .swiper-slide {
	background: #fff;
	font-family: 'Montserrat', sans-serif;
	padding:  30px;
	box-sizing: border-box;
	cursor: pointer;
}

#page2 .slider .swiper .swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
#page2 .slider .swiper-slide .img{
	height: 35%;
	width: 100%;
	text-align: left;
}
#page2 .slider .swiper-slide .img img{
	width: 50px;
	height: 50px;
}
#page2 .slider .swiper-slide .img p{
	margin-top: 20px;
	color: #aaa;
	font-weight: 600;
	font-size: 1.125em;
}
#page2 .slider .swiper-slide .desc{
	height: 75%;
	line-height: 1.4;
	margin-top: 15px;
	font-size: 1em;
	text-align: left;
}
#page2 .slider .swiper-slide .desc p{
	display: -webkit-box;
	text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
	overflow: hidden;
}

#page2 .slider .left .pagination{
	margin-top: 50px;
}

#page2 .slider .left .cv a{
	display: block;
	width: 220px;
	height: 50px;
	border: 1px solid #000;
	text-align: center;
	line-height: 50px;
	letter-spacing: 4px;
	font-size: 1.25em;
}

#page2 .slider .left .control{
	display: inline-block;
	margin-right: 40px;
}
#page2 .slider .left .control .prev,
#page2 .slider .left .control .next{
	display: inline-block;
}
#page2 .slider .left .control .prev{
	margin-right: 5px;
	width: 22px;
	height: 17px;
	overflow: hidden;
	text-indent: -9999px;
	background: url(./prev.png) no-repeat 0 0;
}
#page2 .slider .left .control .next{
	width: 22px;
	height: 17px;
	overflow: hidden;
	text-indent: -9999px;
	background: url(./next.png) no-repeat 0 0;
}

#page2 .slider .left .progress{
	display: inline-block;
	position: relative;
	top: -7px;
	width: 100px;
	height: 3px;
	background-color: #aaaaaa ;
}
#page2 .slider .left .progress .bar{
	position: absolute;
	top: 0;
	width: 20%;
	height: 3px;
	background-color: #d73d3d;
}

#page2 .slider .left .pagination span.num{
	font-weight: 600;
	position: absolute;
	left: -25px;
	top:-5px;
}
#page2 .slider .left .pagination span.total{
	font-weight: 600;
	position: absolute;
	right: -25px;
	top:-5px;
}

@media only screen and (max-width:1100px){
	#page2{
		height: auto;
	}
	#page2 .slider .left{
		width: 100%;
	}
	#page2 .left .pagination{
		width: 100%;
	}

	#page2 .slider{
		position: relative;
		flex-direction: column;
	}
	#page2 .slider .swiper{
		margin-top: 30px;
		padding-left: 0;
		width: 100%;
	}
	#page2 .title{
		width: 100%;
		height: 80px;
	}
}
@media only screen and (max-width:890px){
	#page2 .title{
		width: 100%;
		height: 80px;
	}

	#page2 .inner{
		padding: 0 30px
	}
}
@media only screen and (max-width:576px){
	#page2 .title .name .br{
		display: block;
	}
}