@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&family=Pacifico&family=Roboto:wght@300;400;500;700&display=swap');

#root{
	position: relative;
}
section{
	padding: 100px 0;
	box-sizing: border-box;
	height: 100vh;
	overflow-x: hidden;
}
section .inner{
	margin: 0 auto;
	max-width: 1270px;
	position: relative;
	height: 100%;
	padding: 0 100px;
}
section .title .label h2{
	font-size: 1.125em;
	font-weight: 300;
	color: #d73d3d;
	margin-bottom: 30px;
}
section .title .name{
	font-family: 'Montserrat', sans-serif;
	font-size: 3.125em;
	font-weight: 700;
	margin-bottom: 30px;
}