#header{
	height: 100vh;
	position: relative;
	background-color: #000;
}
#header .inner{
	max-width: 1270px;
	transition: 0.3s;
}

#header .top{
	position: fixed;
	width: 100%;
	height: 70px;
	z-index: 9999;
	transition: 0.3s;
}
#header.dark .top{
	background-color: #fff;
	box-shadow: 0 20px 20px -10px #eaeaea;
}
#header .top .inner{
	margin: 0 auto;
	max-width: 1270px;
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
	padding: 0 100px;
}

#header .top .logo a{
	font-family: 'Pacifico', cursive;
	font-weight: 800;
	display: block;
	font-size: 1.5em;
	color: #fff;
	line-height: 50px;
}
#header .top .logo.dark a{
	color: #d73d3d;
}


#gnb{
	transition: 0.3s;
}
#gnb > ul > li{
	display: inline-block;
}
#gnb ul li a{
	display: block;
	padding: 0 1.875em;
	color: #fff;
	line-height: 50px;
	position: relative;
	transition: 0.3s;
}
#gnb.dark ul li a{
	color: #666;
	font-weight: 500;
}
#gnb ul li:hover a::after,
#gnb ul li.active a::after{
	content: "";
	position: absolute;
	left: 25%;
	bottom: -10px;
	width: 50%;
	height: 2px;
	background-color: #d73d3d;
}

#lnb{
	z-index: 9999;
	position: fixed;
	left: 20px;
	bottom: 0;
	transform: translateY(-50%);
	transform: rotate(-90deg);
	transform-origin:  0 0;
}
#lnb ul li{
	position: relative;
	display: inline-block;
}
#lnb li a{
	position: relative;
	display: block;
	padding:  0 20px;
	color: #fff;
	font-size: 1em;
	transition: 0.5s;
}
#lnb.dark li a{
	color: #666;
}
#lnb li.active a{
	color: #d73d3d;
	font-weight: 600;
}

#m_gnb{
	/* display: none; */
	position: fixed;
	padding: 100px 40px;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	/* height: 100%;
	width: 100%; */
	box-sizing: border-box;
	z-index: 9999;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	background-color: #f4f4f4;
	transform: translateY(-100%);
	transition: transform 0.4s;
}
#m_gnb.active{
	transform: translateY(0);
}
#m_gnb .mobile{
	position: relative;
	transform: translateY(50px);
}
#m_gnb .mobile::after{
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	bottom: 5.938em;
	width: 20%;
	height: 3px;
	background-color: #333;
	transform: translateX(-50%);
}
#m_gnb .menu{
	margin-bottom: 50px;
}
#m_gnb .menu li a{
	display: inline-block;
	line-height: 2;
	font-size: 2.5em;
	font-weight: 300;
	position: relative;
}
#m_gnb .menu li.active a{
	font-weight: 700;
}
#m_gnb .menu li.active a::after{
	content: "";
	display: block;
	position: absolute;
	left: -25px;
	top: 0;
	z-index: -1;
	width: 40px;
	height: 40px;
	background-color: #d73d3d;
	border-radius: 50%;
}
#m_gnb .contact li a{
	display: block;
	padding: 0.4em 0;
	font-size: 1.250em;
	letter-spacing: 2px;
}
#m_gnb .contact li a img{
	margin-right: 1em;
	vertical-align: middle;
	width: 20px;
}

#header .tab{
	position: fixed;
	right: 30px;
	top: 1.98em;
	display: none;
	z-index: 10000;
}
#header .tab a{
	display: block;
	width: 20px;
	height: 20px;
}
#header .tab span{
	position: absolute;
	right: 0;
	display: block;
	width: 15px;
	height: 2.5px;
	background-color: #fff;
	transition: 0.4s;
}
#header .tab.dark span{
	background-color: #d73d3d;

}

#header .tab li:nth-child(1) span{
	top: 0;
}
#header .tab li:nth-child(2) span{
	top: 5px;
	width: 21px;
}
#header .tab li:nth-child(3) span{
	top: 10px;
}
#header .tab a:hover li:nth-child(1) span,
#header .tab a:hover li:nth-child(3) span{
	width: 21px;
}
#header .tab a:hover li:nth-child(2) span{
	width: 15px;
}

#header .tab.active span{
	background-color: #000;
}
#header .tab.active li:nth-child(1) span{
	width: 17px;
	top: 5px;
	transform: rotate(45deg);
}
#header .tab.active li:nth-child(2) span{
	display: none;
}
#header .tab.active li:nth-child(3) span{
	width: 17px;
	top: 5px;
	transform: rotate(-45deg);
}

#header .video_area {
	height: 100vh;
	overflow: hidden;
}
#header .video_area video {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

#header .main_txt{
	position: absolute;
	width: 100%;
	left: 50%;
	top:50%;
	transform: translate(-50%,-50%);
	font-size: 1em;
}
#header .main_txt .inner{
	max-width: 1270px;
	margin: 0 auto;
	padding: 0 100px;
}

#header .main_txt dt{
	color: #fff;
	font-size: 1.250em;
	transform: translateY(50px);
	opacity: 0;
	transition: opacity 0.3s,transform 0.8s;
	transition-delay: 0.3s;
}
#header .main_txt dd{
	font-family: 'Montserrat', sans-serif;
	margin-top: 10px;
	color: #fff;
	font-size: 5em;
	line-height: 1.2;
	font-weight: 800;
	text-transform: uppercase;
	transform: translateY(50px);
	opacity: 0;
	transition: opacity 0.3s,transform 0.8s;
	transition-delay: 0.6s;
}
#header.active .main_txt dt,
#header.active .main_txt dd{
	opacity: 1;
	transform: translateY(0);
}
#header .main_txt dd .br{
	display: none;
}

#header .main_txt dd span{
	color: #d73d3d;
	transition: 0.6s;
}
#header .main_txt dd.desc{
	font-size: 1.25em;
	font-weight: 400;
}
#header .main_txt .controller{
	position: relative;
	padding-top: 20px;
	display: flex;
	align-items: center;
}
#header .main_txt .controller >div{
	display: inline-block;
}

#header .main_txt .controller .pagination{
	margin-right: 10px;
}
#header .main_txt .controller .pagination li{
	display: inline-block;
	position: relative;
	top: -2px;
	font-size: 0;
	vertical-align: middle;
}
#header .main_txt .controller .pagination li a{
	display: block;
	width:5px;
	height:5px;
	border-radius: 100%;
	margin-left: 16px;
	background-color: rgba(255,255,255,.5);
	overflow: hidden;
	text-indent: -9999px;
}	
#header .main_txt .controller .pagination li.active a{
	background-color: #fff;
}
#header .main_txt .controller svg {
	position: absolute;
	top: 12px;
	width: 34px;
	height: 34px;
	z-index: 1000;
}
#header .main_txt .controller svg circle {
	stroke: #fff;
	fill: none;
	stroke-dasharray: 200; 
	stroke-dashoffset: 200; 
	stroke-width: 4px;
	transform: rotate(-90deg);
	transform-origin: center;
}
#header .main_txt .controller.active svg circle {
	animation: stroke_animation 3s;
	animation-fill-mode: both; 
} 


#header .main_txt .controller .pagination li:first-child{
	margin-left: 0;
}
@keyframes stroke_animation {
	from {
		stroke-dashoffset: 200;
	}
	to {
		stroke-dashoffset: 0;
	}
}

#header .main_txt .controller #pause_play{
	margin-left: 15px;
}
#header .main_txt .controller .pause{
	background: url(./btn_pause.png) no-repeat 0 0;
	width: 20px;
	height: 20px;
	text-indent: -9999px;
	overflow: hidden;
	vertical-align: middle;
}
#header .main_txt .controller .play{
	background: url(./btn_play.png) no-repeat 0 0;
	background-size: contain;
	width: 20px;
	height: 20px;
	text-indent: -9999px;
	overflow: hidden;
}
#header .main_txt .controller .btn{
	margin-left: 15px;
}
#header .main_txt .controller .btn .prev{
	background: url(./btn_prev.png) no-repeat 0 0;
	background-size: cover;
	width: 20px;
	height: 15px;
	text-indent: -9999px;
	overflow: hidden;
}
#header .main_txt .controller .btn .next{
	background: url(./btn_next.png) no-repeat 0 0;
	background-size: cover;
	width: 20px;
	height: 15px;
	text-indent: -9999px;
	overflow: hidden;
}
#header .main_txt .controller .btn .next:hover{
	background: url(./btn_next_hover.png) no-repeat 0 0;
	background-size: cover;
}
#header .main_txt .controller .btn .prev:hover{
	background: url(./btn_prev_hover.png) no-repeat 0 0;
	background-size: cover;
}

#header .main_txt .controller .btn > div{
	display: inline-block;
	margin-left: 10px;
}

#header .email{
	position: fixed;
	right:20px;
	bottom: 10px;
	transform: rotate(90deg);
	transform-origin:right;
	z-index: 9000;
}
#header .email a{
	font-family: 'Montserrat', sans-serif;
	display: block;
	color: #d73d3d;
	letter-spacing: 5px;
	font-size: 1em;
	font-weight: 600;
}

.icon-scroll {
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-bottom: 25px;
    left: 50%;
    bottom: 20px;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.icon-scroll .txt-scroll {
    font: 13px "gothamL", sans-serif;
    color: #fff;
}
.icon-scroll .ico-scroll {
    display: block;
    margin-top: 8px;
}
.icon-scroll .arrows-scroll span {
    position: absolute;
    display: block;
    bottom: 0;
    left: 50%;
    width: 10px;
    height: 10px;
    margin-left: -6px;
    border-left: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-animation: scrollDown 2s infinite;
    animation: scrollDown 2s infinite;
    opacity: 0;
    box-sizing: border-box;
}
.icon-scroll .arrows-scroll span:nth-of-type(1) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
.icon-scroll .arrows-scroll span:nth-of-type(2) {
    bottom: 7px;
    -webkit-animation-delay: 0.15s;
    animation-delay: 0.15s;
}
.icon-scroll .arrows-scroll span:nth-of-type(3) {
    bottom: 14px;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}
@-webkit-keyframes scrollDown {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes scrollDown {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@media only screen and (max-width:870px){
	#header .main_txt dd{
		font-size: 3.125em;
	}
	#header .main_txt .inner{
		padding: 0 30px;
	}
	#header .main_txt dd .br{
		display: block;
	}
	#header .main_txt dd{
		font-size: 3.125em;
	}
	#header .main_txt dt,
	#header .main_txt dd.desc{
		font-size: .875em;
	}
	#header .top .inner{
		padding: 0 30px;
	}
	#gnb{
		display: none;
	}
	#header .tab{
		display: block;
	}
	#lnb{
		display: none;
	}
	.icon-scroll{
		display: none;
	} 
}
@media only screen and (max-width:576px){
	#header{
		height: auto;
	}
	#header .video_area{
		height: 80vh;
	}
	#header .main_txt dt,
	#header .main_txt dd.desc{
		font-size: .875em;
	}
	#header .main_txt dd{
		font-size: 2.75em;
	}

	#header .top .logo{
		left: 30px;
	}


}
@media only screen and (max-width:410px){
	#m_gnb .menu li a{
		font-size: 2em;
	}
	#m_gnb .contact li a{
		font-size: 1em;
		letter-spacing: 2px;
	}
}
@media only screen and (orientation:landscape){
	#m_gnb .menu li a{
		font-size: 2em;
	}
	#m_gnb .contact li a{
		font-size: 1em;
		letter-spacing: 2px;
	}
	#m_gnb{
		overflow: auto;
	}
}